// WebSocketÊúçÂä°Á±ª
class WebSocketService {
  constructor() {
    this.ws = null;
    this.reconnectTimer = null;
    this.reconnectDelay = 5000;
    this.wsUrl = "";
    this.callbacks = {};
    this.connectionStatus = 'disconnected';
    this.autoReconnect = true;
    this.isManualDisconnect = false;
  }

  // ËøûÊé•WebSocket
  connect(settings = {}) {
    try {
      // Â∫îÁî®Áî®Êà∑ËÆæÁΩÆ
      if (settings.wsUrl) {
        this.wsUrl = settings.wsUrl;
      }
      if (settings.reconnectInterval) {
        this.reconnectDelay = settings.reconnectInterval * 1000;
      }
      if (settings.autoReconnect !== undefined) {
        this.autoReconnect = settings.autoReconnect;
      }

      // ÈáçÁΩÆÊâãÂä®Êñ≠ÂºÄÊ†áÂøó
      this.isManualDisconnect = false;
      this.updateConnectionStatus('connecting', 'Ê≠£Âú®ËøûÊé•...');

      this.ws = new WebSocket(this.wsUrl);

      this.ws.onopen = () => {
        console.log('WebSocketËøûÊé•Â∑≤Âª∫Á´ã');
        this.updateConnectionStatus('connected', 'Â∑≤ËøûÊé•');

        if (this.reconnectTimer) {
          clearTimeout(this.reconnectTimer);
          this.reconnectTimer = null;
        }
      };

      this.ws.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          console.log('Êî∂Âà∞Êï∞ÊçÆ:', data);

          if (Array.isArray(data)) {
            this.processDevicesData(data);
          } else {
            console.warn('Êï∞ÊçÆÊ†ºÂºè‰∏çÊ≠£Á°ÆÔºåÊúüÊúõÊï∞ÁªÑÊ†ºÂºè');
          }
        } catch (error) {
          console.error('Êï∞ÊçÆËß£ÊûêÂ§±Ë¥•:', error);
          this.updateConnectionStatus('error', 'Êï∞ÊçÆËß£ÊûêÈîôËØØ');
        }
      };

      this.ws.onerror = (error) => {
        console.error('WebSocketÈîôËØØ:', error);
        this.updateConnectionStatus('error', 'ËøûÊé•ÈîôËØØ');
      };

      this.ws.onclose = () => {
        console.log('WebSocketËøûÊé•Â∑≤ÂÖ≥Èó≠');

        // Âè™ÊúâÂú®ÈùûÊâãÂä®Êñ≠ÂºÄÊó∂ÊâçÊòæÁ§∫Êñ≠ÂºÄËøûÊé•ÈÄöÁü•
        if (!this.isManualDisconnect) {
          this.updateConnectionStatus('disconnected', 'ËøûÊé•Â∑≤Êñ≠ÂºÄ');
        }

        // Ê†πÊçÆÁî®Êà∑ËÆæÁΩÆÂÜ≥ÂÆöÊòØÂê¶Ëá™Âä®ÈáçËøû
        if (this.autoReconnect) {
          if (!this.isManualDisconnect) {
            this.updateConnectionStatus('disconnected', 'ËøûÊé•Â∑≤Êñ≠ÂºÄÔºåÂáÜÂ§áÈáçËøû...');
          }
          this.reconnectTimer = setTimeout(() => {
            console.log('Â∞ùËØïÈáçÊñ∞ËøûÊé•...');
            this.connect();
          }, this.reconnectDelay);
        } else {
          this.updateConnectionStatus('disconnected', 'ËøûÊé•Â∑≤Êñ≠ÂºÄÔºåËá™Âä®ÈáçËøûÂ∑≤Á¶ÅÁî®');
        }
      };

    } catch (error) {
      console.error('WebSocketËøûÊé•Â§±Ë¥•:', error);
      this.updateConnectionStatus('error', 'ËøûÊé•Â§±Ë¥•');

      // Ê†πÊçÆÁî®Êà∑ËÆæÁΩÆÂÜ≥ÂÆöÊòØÂê¶Ëá™Âä®ÈáçËøû
      if (this.autoReconnect) {
        this.reconnectTimer = setTimeout(() => {
          this.connect();
        }, this.reconnectDelay);
      }
    }
  }

  // Êõ¥Êñ∞ËøûÊé•Áä∂ÊÄÅ
  updateConnectionStatus(status, message) {
    this.connectionStatus = status;
    if (this.callbacks.onStatusChange) {
      this.callbacks.onStatusChange(status, message);
    }
  }

  // Â§ÑÁêÜËÆæÂ§áÊï∞ÊçÆ
  processDevicesData(devices) {
    if (this.callbacks.onData) {
      this.callbacks.onData(devices);
    }
  }

  // Ê≥®ÂÜåÂõûË∞ÉÂáΩÊï∞
  on(eventName, callback) {
    this.callbacks[eventName] = callback;
  }

  // ÂÖ≥Èó≠ËøûÊé•
  disconnect() {
    // ËÆæÁΩÆÊâãÂä®Êñ≠ÂºÄÊ†áÂøó
    this.isManualDisconnect = true;

    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.close();
    }
    if (this.reconnectTimer) {
      clearTimeout(this.reconnectTimer);
      this.reconnectTimer = null;
    }
  }
}

export default new WebSocketService();