<template>
  <div class="container">
    <!-- ËÆæÁΩÆÊåâÈíÆ -->
    <button class="settings-btn" @click="showSettings">
      ‚öôÔ∏è
    </button>

    <!-- Ê∂àÊÅØÊèêÁ§∫ -->
    <div v-if="notification.show" :class="['notification', notification.type]" @click="hideNotification">
      {{ notification.message }}
      <button class="notification-close" @click.stop="hideNotification">√ó</button>
    </div>

    <!-- Ê±áÊÄª‰ø°ÊÅØÂå∫Âüü -->
    <section class="summary-section">
      <h2 class="section-title">üìà Êï∞ÊçÆÊ±áÊÄª</h2>

      <!-- ËÆæÂ§áÁªüËÆ°Âç°Áâá -->
      <SummaryCards :summary="summary" />

      <!-- Á°¨‰ª∂Ê±áÊÄªÂõæË°® -->
      <HardwareCharts :summary="summary" />
    </section>

    <!-- ËÆæÂ§áËØ¶ÊÉÖÂå∫Âüü -->
    <DeviceList
      :devices="devices"
      :loading-message="loadingMessage"
    />

    <!-- ËÆæÁΩÆÊ®°ÊÄÅÊ°Ü -->
    <SettingsModal
      :is-visible="isSettingsVisible"
      :is-required="isSettingsRequired"
      @close="hideSettings"
      @settings-updated="onSettingsUpdated"
    />
  </div>
</template>

<script>
import SummaryCards from '../components/SummaryCards.vue';
import HardwareCharts from '../components/HardwareCharts.vue';
import DeviceList from '../components/DeviceList.vue';
import SettingsModal from '../components/SettingsModal.vue';
import webSocketService from '../services/websocket.js';

export default {
  name: 'MonitorView',
  components: {
    SummaryCards,
    HardwareCharts,
    DeviceList,
    SettingsModal
  },
  data() {
    return {
      devices: [],
      connectionStatus: 'disconnected',
      connectionMessage: 'Êú™ËøûÊé•',
      lastUpdateTime: new Date(),
      loadingMessage: 'Ê≠£Âú®Âª∫Á´ãËøûÊé•...',
      timeUpdateInterval: null,
      isSettingsVisible: false,
      isSettingsRequired: false,
      notification: {
        show: false,
        message: '',
        type: 'info', // 'success', 'error', 'info', 'warning'
      }
    };
  },
  computed: {
    summary() {
      if (this.devices.length === 0) {
        return {
          total: 0,
          windows: 0,
          linux: 0,
          other: 0,
          totalCpuCores: 0,
          avgCpuUsage: 0,
          totalMemory: 0,
          usedMemory: 0,
          totalDisk: 0,
          usedDisk: 0
        };
      }

      const summary = {
        total: this.devices.length,
        windows: 0,
        linux: 0,
        other: 0,
        totalCpuCores: 0,
        totalCpuUsage: 0,
        totalMemory: 0,
        usedMemory: 0,
        totalDisk: 0,
        usedDisk: 0
      };

      this.devices.forEach(device => {
        // ÁªüËÆ°Êìç‰ΩúÁ≥ªÁªü
        const platform = device.osInfo?.platform?.toLowerCase() || '';
        if (platform.includes('windows')) {
          summary.windows++;
        } else if (platform.includes('linux')) {
          summary.linux++;
        } else {
          summary.other++;
        }

        // CPUÁªüËÆ°
        summary.totalCpuCores += device.cpuInfo?.cpuCount || 0;
        summary.totalCpuUsage += device.cpuInfo?.cpuUsage || 0;

        // ÂÜÖÂ≠òÁªüËÆ°
        const totalMem = device.memInfo?.totalMemMb || 0;
        const usedMem = device.memInfo?.usedMemMb || 0;
        summary.totalMemory += totalMem;
        summary.usedMemory += usedMem;

        // Á£ÅÁõòÁªüËÆ°
        let totalDisk = device.driveInfo?.totalGb || 0;
        let usedDisk = device.driveInfo?.usedGb || 0;

        if (typeof totalDisk === 'string') totalDisk = parseFloat(totalDisk) || 0;
        if (typeof usedDisk === 'string') usedDisk = parseFloat(usedDisk) || 0;

        summary.totalDisk += totalDisk;
        summary.usedDisk += usedDisk;
      });

      // ËÆ°ÁÆóÂπ≥ÂùáCPU‰ΩøÁî®Áéá
      if (summary.total > 0) {
        summary.avgCpuUsage = (summary.totalCpuUsage / summary.total).toFixed(2);
      }

      return summary;
    }
  },
  methods: {
    // ÊòæÁ§∫ËÆæÁΩÆÊ®°ÊÄÅÊ°Ü
    showSettings() {
      this.isSettingsVisible = true;
    },

    // ÈöêËóèËÆæÁΩÆÊ®°ÊÄÅÊ°Ü
    hideSettings() {
      this.isSettingsVisible = false;
    },

    // ÊòæÁ§∫ÈÄöÁü•Ê∂àÊÅØ
    showNotification(message, type = 'info', duration = 3000) {
      this.notification = {
        show: true,
        message: message,
        type: type
      };

      if (duration > 0) {
        setTimeout(() => {
          this.hideNotification();
        }, duration);
      }
    },

    // ÈöêËóèÈÄöÁü•Ê∂àÊÅØ
    hideNotification() {
      this.notification.show = false;
    },

    // ÊòæÁ§∫ËÆæÁΩÆÊ®°ÊÄÅÊ°ÜÔºàÂøÖÈúÄÊ®°ÂºèÔºâ
    showRequiredSettings() {
      this.isSettingsRequired = true;
      this.isSettingsVisible = true;
    },

    // ÈöêËóèËÆæÁΩÆÊ®°ÊÄÅÊ°ÜÔºàÂøÖÈúÄÊ®°ÂºèÔºâ
    hideRequiredSettings() {
      this.isSettingsRequired = false;
      this.isSettingsVisible = false;
    },

    // Âä†ËΩΩÁî®Êà∑ËÆæÁΩÆÂπ∂ËøûÊé•WebSocket
    loadAndConnect() {
      // ‰ªélocalStorageÂä†ËΩΩËÆæÁΩÆ
      let settings = {};
      const savedSettings = localStorage.getItem('monitorSettings');

      if (savedSettings) {
        try {
          settings = JSON.parse(savedSettings);
        } catch (error) {
          console.error('Failed to load settings:', error);
          settings = {};
        }
      }

      // Â¶ÇÊûúÊ≤°ÊúâWebSocketÂú∞ÂùÄÔºåÊòæÁ§∫ËÆæÁΩÆÁïåÈù¢
      if (!settings.wsUrl || !settings.wsUrl.trim()) {
        this.showNotification('ËØ∑ÂÖàÈÖçÁΩÆWebSocketÊúçÂä°Âô®Âú∞ÂùÄ', 'warning');
        this.showRequiredSettings();
        return;
      }

      // ‰ΩøÁî®ËÆæÁΩÆËøûÊé•WebSocket
      this.showNotification('Ê≠£Âú®ËøûÊé•WebSocketÊúçÂä°Âô®...', 'info');
      webSocketService.connect(settings);
    },

    // Â§ÑÁêÜËÆæÁΩÆÊõ¥Êñ∞
    onSettingsUpdated(settings) {
      // ÈáçÊñ∞ËøûÊé•WebSocket‰ΩøÁî®Êñ∞ÁöÑËÆæÁΩÆ
      webSocketService.disconnect();
      this.showNotification('Ê≠£Âú®ÈáçÊñ∞ËøûÊé•WebSocketÊúçÂä°Âô®...', 'info');
      setTimeout(() => {
        webSocketService.connect(settings);
      }, 100);
    },

    // Â§ÑÁêÜÊé•Êî∂Âà∞ÁöÑËÆæÂ§áÊï∞ÊçÆ
    processDevicesData(devices) {
      if (devices.length === 0) {
        this.devices = [];
        this.loadingMessage = 'ÊöÇÊó†ËÆæÂ§áÊï∞ÊçÆ';
        return;
      }

      // Â¶ÇÊûúÊòØÈ¶ñÊ¨°Êé•Êî∂Âà∞ËÆæÂ§áÊï∞ÊçÆÔºåÊòæÁ§∫ÊàêÂäüÈÄöÁü•
      if (this.devices.length === 0 && devices.length > 0) {
        this.showNotification(`ÊàêÂäüËøûÊé•Âà∞ ${devices.length} Âè∞ËÆæÂ§á`, 'success', 3000);
      }

      this.devices = devices;
      this.updateTimestamp();
    },

    // Êõ¥Êñ∞Êó∂Èó¥Êà≥
    updateTimestamp() {
      this.lastUpdateTime = new Date();
    },

    // Êõ¥Êñ∞ËøûÊé•Áä∂ÊÄÅ
    updateConnectionStatus(status, message) {
      this.connectionStatus = status;
      this.connectionMessage = message;

      if (status === 'connected') {
        this.loadingMessage = 'Ê≠£Âú®Êé•Êî∂ËÆæÂ§áÊï∞ÊçÆ...';
        this.showNotification('WebSocketÊúçÂä°Âô®ËøûÊé•ÊàêÂäüÔºÅ', 'success');
      } else if (status === 'disconnected') {
        this.loadingMessage = 'ËøûÊé•Â∑≤Êñ≠ÂºÄÔºåÂáÜÂ§áÈáçËøû...';
        this.showNotification('WebSocketËøûÊé•Â∑≤Êñ≠ÂºÄ', 'warning');
      } else if (status === 'error') {
        this.loadingMessage = 'ËøûÊé•Âá∫ÈîôÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•';
        this.showNotification('WebSocketËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊúçÂä°Âô®Âú∞ÂùÄÂíåÁΩëÁªúËøûÊé•', 'error');
      }
    }
  },
  mounted() {
    // ËÆæÁΩÆWebSocketÂõûË∞É
    webSocketService.on('onData', this.processDevicesData);
    webSocketService.on('onStatusChange', this.updateConnectionStatus);

    // Âä†ËΩΩÁî®Êà∑ËÆæÁΩÆÂπ∂ËøûÊé•WebSocket
    this.loadAndConnect();

    // ÂÆöÊúüÊõ¥Êñ∞Êó∂Èó¥ÊòæÁ§∫
    this.timeUpdateInterval = setInterval(this.updateTimestamp, 1000);
  },
  beforeDestroy() {
    // Ê∏ÖÁêÜÂÆöÊó∂Âô®
    if (this.timeUpdateInterval) {
      clearInterval(this.timeUpdateInterval);
      this.timeUpdateInterval = null;
    }

    // Êñ≠ÂºÄWebSocketËøûÊé•
    webSocketService.disconnect();
  }
};
</script>